import React, { Component } from 'react';
import {Carousel} from "antd"
import c1 from "./c1.jpeg"
import c2 from "./c2.jpg"
import c3 from "./c3.jpg"
import c4 from "./c4.jpg"
import c5 from "./c5.jpg"
import c6 from "./c6.jpg"

export default class Car extends Component{
  render(){
    return(
          <div >
            <Carousel autoplay effect="fade" style={{width:"100%"}}>
                      <div style={{height:"400px"}}><img src={c1}/></div>
                      <div style={{width:"100%",height:"400px"}}><img src={c2} style={{width:"120%"}}/></div>
                      <div style={{width:"100%",height:"400px"}}><img src={c3} style={{width:"120%"}}/></div>
                      <div style={{width:"100",height:"400px"}}><img style={{width:"120%"}} src={c4}/></div>
                      <div style={{width:"100%",height:"400px"}}><img style={{width:"120%"}} src={c5}/></div>
                      <div style={{width:"100%",height:"400px"}}><img style={{width:"120%"}} src={c6}/></div>
            </Carousel>


          </div>

    )
  }
}



