import React, { Component } from 'react';
import logo from "./logo.png"
import Name from "./Name.png"
import {Anchor} from "antd"




export default class Appbar extends Component{
  render(){
    return(

      <div>
     <div style={{height:"55px",width:"100%",backgroundColor:"#fff",display:"flex"}}>

       <div style={{height:"50%",alignItems:"center",padding:"15px"}}>
      <img src={logo} style={{height:"50%"}}/>
      </div>
      <div style={{padding:"10px"}}>
      <img src={Name} style={{height:"80%"}}/>
        </div>
        </div>
        <div style={{height:"40px",width:"100%",backgroundColor:"#37ACD9",display:"flex"}}>
          <div style={{display:"flex",justifyContent:"space-evenly",padding:"13px",width:'100%',color:"#fff",fontSize:"13px"}}>
          <div><a href="#home" style={{color:"#fff"}}>Home</a></div>
          <div><a href="#event" style={{color:"#fff"}}>Events</a></div>
          <div><a href="#package" style={{color:"#fff"}}>Packages</a></div>
          <div>About</div>
          </div>
        </div>
     </div>
    )
  }
}



