import React, { Component } from 'react';
import Logo from "./logo1.jpg"
export default class About extends Component{
  render(){
    return(
          <div hover style={{height:"300px",width:"100%",backgroundColor:"#2E2C2D",display:"flex",justifyContent:"space-between",padding:"4%"}}>
            <div style={{display:"flex"}} >
              <div style={{display:"flex"}}>
              <div style={{height:"183.3px",width:"183.3px"}}>
                <img src={Logo}style={{height:"183.3px",width:"183.3px"}}/>
              </div>
              <div>
                <h1 style={{fontSize:"20px",fontWeight:"bold",marginLeft:"10px"}}>About The Company</h1>
              </div>
            </div>
          </div>
          <div>
            <h1 style={{fontSize:"20px",fontWeight:"bold",marginLeft:"10px"}}>Contact Us</h1>
          </div>
        </div>

    )
  }
}



